<template>
<v-container justify-lg-center >
  cells alive : {{  cellsAlive }}
  cells created : {{cellsCreated}}
<!-- <v-btn mb-4 @click="update()">update</v-btn>
<v-btn mb-4 @click="reset()">reset</v-btn>
<v-btn mb-4 @click="randomSeed()">random</v-btn> -->

  <div class="grid" 
      @mousedown="isMouseDown = true"
      @mouseup="isMouseDown = false"
      @mouseleave="isMouseDown = false">
 <div class="col" 
 
   v-for=" (col,indexX) in gridbox" :key='indexX'
  >
 <Cells 
 v-for=" (status,indexY) in col" :key='indexY'
 :status="status"
 :x-pos="indexX"
 :y-pos="indexY"
  :is-mouse-down="isMouseDown"
          @wasUpdated="updateCellCount(status)"
 >
{{status}}</Cells>
</div>
  </div>
</v-container>
</template>


<script>
// @ is an alias to /src
import Cells from '@/components/cells.vue'

export default {
  name: 'Grid',
  props:{

    message: {
      default: '',
      type: String,
    }
  },
  components: {
    Cells
  }
  ,setup(){
 const width = 20;
      const height = 10;
      var  gridbox =[];
      // Stats that get passed down to the app-stats component
      let currentTick = 0;
      let cellCount = 0;
      let cellsAlive = 0;
      let cellsCreated = 0;
      // A prop that gets used by the app-cell component (drag)
      let isMouseDown = (false);
      return {
        width,
        height,
        gridbox,
        currentTick,
        cellCount,
        cellsAlive,
        cellsCreated,
        isMouseDown
      }    
  }, created(){
this.creategrid();
  },
  data(){
      return{

                  gridbox:[],
                  width :60,
                height : 40,
                isMouseDown:false,
                  currentTick : 0,
       cellCount :0,
       cellsAlive :0,
       cellsCreated :0
      }

      
  },
  computed: {},
  watch: {
    /**
     * Watches for changes in the message prop
     * that gets passed down from the App component
     * and then handles the input on a specific tick.
     *
     * @param {string} val - the value
     */
    message: function(val) {
      if (val === 'update') {
        //alert('update callled');
        this.update();
        this.currentTick++;
      } else if (val === 'reset') {
        this.reset();
      } else if (val === 'random') {
        this.randomSeed();
      } 
    },
  },
  methods: {
    creategrid(){
     // alert('reached cretegrid');
      for(var i=0; i<this.width; i++)
      {
        this.gridbox[i]=[];
        for(var j=0; j<this.height; j++)
        {
         // alert('reached');
          this.gridbox[i][j]={status:false};
         // alert('status is'+this.gridbox[i][j]);
        }
      }
      // alert('gird initialized');
      // alert('grid valude of 3,1 is'+this.gridbox[3][1]);
    },

    updateCellCount: function(status) {
  //    alert('updte status is :'+status)
      if (status) {
        this.cellsAlive++;
        this.cellsCreated++;
      } else {
        this.cellsAlive--;
      }
    },

   
    /**
     * Changes the 'status' object property
     * of a specific cell to the one requested
     * in the param.
     *
     * @param {number} x - the x position
     * @param {number} y - the y position
     * @param {boolean} alive - the new boolean
     */
    
    setCell: function(x, y, alive) {
      //alert('setcell reached'+x+y+alive+'value : '+this.gridbox[x][y].status);
      if (this.gridbox[x][y].status != alive) {
       this.gridbox[x][y].status= alive;
     // alert('updated '+x+y+alive+'value : '+this.gridbox[x][y].status);
        
        this.updateCellCount(alive);
      }
      // let row = this.gridList[x];
      // row.splice(y, 1, {isAlive: true});
      // this.gridList.splice(x, 1, row);
    },
    /**
     * The main function that updates the grid
     * every tick based on the game of life rules.
     */
    update: function() {
      let tempArr=[];
      for (let i = 0; i < this.width; i++) {
        tempArr[i] = [];
        for (let j = 0; j < this.height; j++) {
          let status = this.gridbox[i][j].status;
          // alert('current status'+status);
          let neighbours = this.getNeighbours(i, j);
          // alert('neigbours resolved');

let result = false;
          // Rule 1:
          // Any live cell with fewer than two live neighbours dies,
          // as if by under population
          if (status && neighbours < 2) {
            result = false;
          }
          // Rule 2:
          // Any live cell with two or three neighbours lives on
          // to the next generation
          if ((status && neighbours == 2) || neighbours == 3) {
            result = true;
          }
          // Rule 3:
          // Any live cell with more than three live neighbours dies,
          // as if by overpopulation
          if (status && neighbours > 3) {
            result = false;
          }
          // Rule 4:
          // Any dead cell with exactly three live neighbours becomes
          // a live cell, as if by reproduction
          if (!status && neighbours == 3) {
            result = true;
          }
          tempArr[i][j] = result;
          
       }
      }
      // set new gridList content
      for (let i = 0; i < this.width; i++) {
        for (let j = 0; j < this.height; j++) {
          this.setCell(i, j, tempArr[i][j]);
        }
      }
    },
     reset: function() {
      this.currentTick = 0;
      this.cellsAlive = 0;
      this.cellsCreated = 0;
      this.gridbox.forEach((col) => {
        col.forEach((cell) => {
          cell.status = false;
        });
      });
    },
    /**
     * Populates and overwrites gridList with cells.
     */
    randomSeed: function() {
      for (let i = 0; i < this.width; i++) {
        for (let j = 0; j < this.height; j++) {
          let rand = Math.random();
          if (rand < 0.2) {
            this.setCell(i, j, true);
          } else {
            this.setCell(i, j, false);
          }
        }
      }
    },
    /**
     * Returns the amount of neighbours for
     * a specific cell on the grid.
     *
     * @param {number} posX - the x position
     * @param {number} posY - the Y position
     * @return {number} neighbours - amount of neighbours
     */
     getNeighbours: function(posX, posY) {
      let neighbours = 0;
      if (posX <= this.width && posY <= this.height) {
        for (let offsetX = -1; offsetX < 2; offsetX++) {
          for (let offsetY = -1; offsetY < 2; offsetY++) {
            let newX = posX + offsetX;
            let newY = posY + offsetY;
            // check if offset is:
            // on current cell, out of bounds and if isAlive
            // for cell true
            if (
              (offsetX != 0 || offsetY != 0) &&
              newX >= 0 &&
              newX < this.width &&
              newY >= 0 &&
              newY < this.height &&
              this.gridbox[posX + offsetX][posY + offsetY].status == true
            ) {
              neighbours++;
            }
          }
        }
      }
      return neighbours;
    }
  }
}
</script>

<style scoped>
*{
padding: 0px;
margin:0px;
}
.grid{
      border-top: 1px solid #1a0707;
  border-left: 1px solid #1a0707;
  display: flex;
  flex: 1;
  justify-content: center;
}
.col
{
      flex: 1;
  display: flex;
  justify-content: center;
  padding: 0;
  margin: 0 auto;
  flex-direction: column;
}

</style>





