<template>
<v-container justify-lg-center>
  
    <h1 style="text-align: center;">This is a game of life</h1>
    Status:{{isRunning?'Running':'Stoped'}}
    <v-icon>play_arrow</v-icon>
  
<v-btn mb-4 @click="messageupdate('update')">
Play  </v-btn>
<v-btn mb-4 @click="messageupdate('reset')">reset</v-btn>
<v-btn mb-4 @click="messageupdate('random')">random</v-btn>
<Grid :message='message' :key='componentKey'></Grid>
</v-container>
</template>


<script>
// @ is an alias to /src
import Grid from '../components/grid.vue'
import {setInterval, clearInterval} from 'timers';
//import {  nextTick } from 'vue';
export default {
  name: 'Gamelife',
  components: {
        Grid
    
  },
  data(){
    return{
message:'',
      componentKey: 0,
      speed :100,
      intervalID:0,
      isRunning:false

    }
  },
  methods:{
    messageupdate(msg){
this.message=msg;
if(msg=='update'){
this.isRunning=true;
}else{
this.isRunning=false;

}
    this.restartInterval();
      this.$nextTick(this.resetMessage);
//this.forceRerender();
    },
      resetMessage(){
      this.message = '';
    },
    restartInterval: function() {
      clearInterval(this.intervalID);
      if (this.isRunning) {
        this.intervalID = setInterval(
          this.messageupdate,
          50000 / this.speed,
          'update'
        );
      }
    }, forceRerender() {
      this.componentKey += 1;
      alert('renerrequest raised');
    }
  
  }
}
</script>







