<template>
<div  :class="status.status ? 'alive' : 'dead'"

    class="cell"
    @mousedown="reborn(true)"
    />
</template>


<script>
import {  reactive } from "vue";
export default {
  name: 'Cells',
   
   props: {
    status: {
      type: Object,
      default: () => ({
        status: false
      })
    },
    xPos: {
      default: -1,
      type: Number,
    },
    yPos: {
      default: -1,
      type: Number,
    },
    isMouseDown: {
      default: false,
      type: Boolean,
    },
  },
  setup(props) {
    let status = reactive(props.status)
    return {
      status
    }
  },
  data(){
      return{

                  
      }

      
  },

  methods: {
    reborn: function(bool) {
      if (bool) {
        //alert('status before chaged'+this.status.status);
       this.status.status=!this.status.status; 
        //alert('status after chage'+this.status.status);
      
        this.$emit('wasUpdated', this.status.status);
      }
    },

  }
}
</script>

<style scoped>
.cell {
  flex: 1;
  user-select: none;
  border-right: 1px solid #1a0707;
  border-bottom: 1px solid #1a0707;
  padding-bottom: 100%;
}
.cell:hover {
  background-color: rgba(132, 26, 26, 0.6);
}
.alive {
  background-color: #bb4747 !important;
}
.dead {
  background-color: #c9cee6 !important;
}
</style>





