<template><v-container >
<v-dialog
      v-model="winner"
      width="50%"
      height="75%"
    >
<v-card  elevation="20"   >
        <v-card-title class="headline blue lighten-3 mt-2 mb-2">
            
GAME OVER     
</v-card-title>
          <v-spacer></v-spacer>

        <v-card-text >
             <div class="font-weight-bold mt-8 mb-2 align-centre ">


         <h1 align-middle dense x-large class="text-center"  > {{status}}
         </h1>

</div>         
        </v-card-text>


        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="primary"
            text
            @click="clear()"
          >
           Play Again
          </v-btn>
          <v-spacer></v-spacer>

        </v-card-actions>
      </v-card>
      </v-dialog>


   <v-card class='cardarea'>Dealer </v-card>

<div class='card'  v-for='id in Dealer' :key='id'> {{id.card}}</div>
   <v-card elevation="2" class='cardarea' color="blue lighten-2"  >Player: 

<div class='card'  v-for='id in Player' :key='id'> {{id.card}}</div>
</v-card>


<v-btn x-large elevation="5" mb-3 @click="hit()">Hit</v-btn>
<v-spacer></v-spacer>
<v-btn x-large elevation="5" mb-3 @click="show()">SHOW</v-btn>
<span :total='Playercount'>
   TOTAL : {{Playercount}} {{status}}
</span>
</v-container>
  
 
</template>

<script>

   export default {
       name:'Bjboard',
      
       data(){
          return {
             Dealer:[],
             Player:[],
             hitcounter:0,
             Playercount:0,
             Dealercount:0,
             status:'',
             winner:false,
             card:''
          }
       },
       methods:{
          hit(
          ){
             var draw=this.draw();
             
             this.Player.push({id:this.hitcounter,card:draw});
               this.Playercount=this.Playercount+draw;
               alert('you drew'+draw);
               if(this.Playercount=='21'){
                  this.status='BLACK Jack for Player';
                  this.winner=true;
               }else if(this.Playercount>'21')
               {
                  this.status='Player went Bust';
                  this.winner=true;
               }
               this.hitcounter+=1;
          },
          show(
          ){

             
              var Ddraw=this.draw();
                          this.Dealer.push({id:this.Dealer.length,card:Ddraw});

             //this.Dealer.push(Ddraw);
               this.Dealercount=this.Dealercount+Ddraw;
               if(this.Dealercount>'21'){
                  this.status='Dealer Went Bust Player won';
                  this.winner=true;
               }
               else if(this.Dealercount>this.Playercount &&this.Dealercount<'22'){
                  this.status='Dealer Won';
                  this.winner=true;
               }else if(this.Dealercount<this.Playercount)
               {
                  this.show();
               }
          },
          draw(){
             return  Math.floor(Math.random() * 13) + 1;

          },
          clear(){
             this.winner='';
             this.hitcounter='0';
             this.Playercount=0;
             this.Dealercount=0;
             this.status='';
             this.Dealer=[];
             this.Player=[];
          }
       }
   }

</script>

<style scoped>

#canvas {
   background: grey;
}
.cardarea{
   display: flex;
   flex-direction: row;
}
.card{
   height: 60px;
   width: 40px;
   display: flex;
   margin-left:2px;
   border: solid black;
      gap:30px;
          justify-content: space-around;
    align-content: flex-start;
    align-items: center;
}

</style>